class Vehicle {
  constructor(name, wheel) {
    this.name = name
    this.wheel = wheel
  }
}
const myVehicle = new Vehicle('Ïö¥ÏÜ°ÏàòÎã®', 2)
console.log(myVehicle)

class Bicycle extends Vehicle {
  constructor(name, wheel) {
    super(name, wheel)
  }
}
const myBicycle = new Bicycle('ÏÇºÏ≤úÎ¶¨', 2)
const daughtersBicycle = new Bicycle('ÏÑ∏Î∞úÏûêÏ†ÑÍ±∞', 3)
